import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Layout from 'layout/Layout';
import Metadata from 'components/Metadata';
import { DisclaimerPageMetadata } from 'configs/PageMetadata';

const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  sectionTitle: {
    paddingBottom: 10,
    fontSize: '1.20rem'
  }
}));

export default function DisclaimerPage() {
  const classes = useStyles();
  return (
    <Layout>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>Disclaimer</Typography>
        <Typography variant="body1">
          The views, thoughts, and opinions expressed in the blog / article  or on pages of this website belong solely to the author, and not necessarily to the author's employer, organization, committee or other group or individual.
        </Typography>
      </Paper>
      <Paper elevation={3} className={classes.root} >
        <Typography variant="h6" className={classes.sectionTitle}>Objection Settlement</Typography>
        <Typography variant="body1">
          In case of any objection over any content of this website, Please reach out to Mr. Bipul Raman <a href="mailto:webmaster@bipul.in">via Email</a>.
        </Typography>
      </Paper>
      <Metadata
        Title={DisclaimerPageMetadata.Title}
        Description={DisclaimerPageMetadata.Description}
      />
    </Layout>
  );
}